.section-partners {
  padding-bottom: 40px; }

.partners-list {
  padding: 0;
  margin: 0 auto;
  max-width: 328px;

  &--mini {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; } }


@include rmin($md) {
  .partners-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 100%; } }

@include rmin($lg) {
  .section-partners {
    padding-top: 85px;
    padding-bottom: 100px;

    .inner-section__head {
      margin-bottom: 60px; } } }
