// buttons
.btn-more-wrap {
  text-align: center; }

button {
  -webkit-appearance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  &:focus {
    outline: none; } }

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  transition: all $transitionSpeed $ease;
  padding: 11px 20px;
  line-height: 22px;
  font-family: $baseFont, $systemFont;
  font-weight: 500;
  font-size: 17px;
  transition: .25s $ease;
  border-radius: $smallRadius;
  box-shadow: none;

  &:focus, &:active {
    outline: none; }

  &--icon {
    display: inline-flex;
    align-items: center;

    .ico {
      margin-right: 15px;
      width: 14px;
      height: 14px;
      text-align: center;
      font-size: 16px;
      font-size: 14px; } }

  &--block {
    display: block;
    width: 100%; }

  &-primary {
    background: $colorPrimary;
    color: $colorYellowLight;

    &:hover {
      background: $colorPrimaryHover;
      color: $colorYellowLight; } }

  &-light {
    background-color: $colorYellowLight;
    color: $colorPrimary;

    &:hover {
      background-color: $colorPrimary;
      color: $colorYellowLight; }

    &--extra {
      background-color: $colorYellowLight;
      color: $colorExtra;

      &:hover {
        background-color: $colorExtra;
        color: $colorYellowLight; } } }

  &-border {
    border: 1px solid $colorPrimary;
    line-height: 20px;

    &:hover {
      background-color: $colorPrimary;
      color: $colorWhite; } }

  &--text {
    background: none;

    &:hover {
      background: none;
      color: $colorPrimaryText; } }


  &--light-color {
    color: $colorYellowLight;

    &:hover {
      color: rgba($colorYellowLight, .8); } }

  &--md {
    font-size: 17px;
    line-height: 22px;
    padding: 13px 20px; }

  &--min {
    font-size: 14px;
    line-height: 18px;
    padding: 6px 16px; }

  &--sm {
    font-size: 15px;
    line-height: 20px;
    padding: 8px 16px; } }

.btn-arrow-top {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  padding: 0;
  background: none;
  outline: none;
  box-shadow: none;

  .ico {
    width: 16px;
    height: 16px;
    color: $colorPrimary;

    &:hover {
      color: $colorPrimaryHover; } } }

@include r($md) {
  .btn--mobile-min {
    font-size: 14px;
    line-height: 18px;
    padding: 6px 16px; } }

@include rmin(lg) {
  .btn {
    padding-left: 20px;
    padding-right: 20px; } }
