.setting-wrapp {
  position: fixed;
  z-index: 220;
  top: 0;
  right: 0;
  height: 100%;

  &.opened {
    top: 0;
    width: 100%;
    background: rgba(0, 0, 0, .56);

    .main-setting {
      transform: translateX(0); }

    .setting-open-button {
      right: 312px;
      top: 18px; } } }

.setting-open-button {
  position: absolute;
  right: 0;
  top: 88px;
  width: 36px;
  height: 32px;
  padding-right: 6px;
  border-radius: 36px 0 0 36px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 20px;
  background-color: #fdfaf2;
  color: $fontColor;
  cursor: pointer;
  transition: all 0.5s, color $transitionSpeed $ease;

  &:hover {
    color: $colorPrimary; } }

.main-setting {
  transform: translateX(100%);
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  padding: 20px;
  overflow-y: auto;
  width: 312px;
  background-color: $colorWhite;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, .05);
  transition: transform 0.5s; }

.main-setting-tt {
  margin-bottom: 17px;
  font-size: 24px;
  line-height: 28px; }

.setting-drop-bl {
  margin-bottom: 25px;

  &__tt {
    position: relative;
    margin-bottom: 8px;
    padding-right: 30px;
    font-weight: 900;
    line-height: 20px;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -4px;
      width: 12px;
      height: 8px;
      background-image: url('../img/ch-arr-bottom.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: 100% 100%; } }

  &:last-child {
    margin-bottom: 0; } }

.setting-options {
  padding: 0;
  margin: 0;

  li {
    display: block;
    padding: 0;
    margin: 0 0 12px; }

  .ui-group {
    margin-bottom: 0; } }

.setting-round-bl {
  min-width: 44px;
  max-width: 44px;
  height: 44px;
  margin-left: 8px;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  overflow: hidden;

  img {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    min-width: 100%;
    transform: translate(-50%, -50%); }

  &.primary-color {
    background-color: $colorPrimary; }

  &.extra-color {
    background-color: $colorExtra; }

  &.more-color {
    background-color: $colorPink; } }

.add-social {
  position: relative;
  margin-top: 18px; }

.social-options {
  position: absolute;
  display: flex;
  align-items: flex-start;
  top: 0;
  left: 0;
  width: 100%;
  line-height: 30px;
  padding: 3px 8px;
  margin: 0;
  background: rgba($colorWhite, .9);

  li {
    margin-right: 16px;
    height: 24px;
    width: 24px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: $colorPrimary;
    cursor: pointer;
    font-size: 14px;

    &:last-child {
      margin-right: 0; } }

  .social-options__close {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    outline: none;
    padding: 0;
    color: $colorRed;
    font-size: 12px; }

  .ico-mono-youtube {
    font-size: 12px; } }

.setting-drop-list {
  margin: 0;
  padding: 0;

  li {
    display: block;
    position: relative;

    &.opened {
      .setting-drop-list__tt {
        margin-bottom: 12px;
        background-color: rgba(31, 32, 65, 0.05);

        &::before {
          transform: rotate(180deg); } } } }

  .ico-mono-eye,
  .ico-mono-blind-eye {
    font-size: 24px; } }

.setting-drop-list__tt {
  position: relative;
  margin-left: -20px;
  margin-right: -20px;
  padding: 12px 48px;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.32px;
  cursor: pointer;
  transition: $transitionSpeed $ease;

  &:hover {
    color: $colorDarkGray; }

  &::before {
    content: '';
    position: absolute;
    left: 21px;
    top: 50%;
    margin-top: -5px;
    width: 12px;
    height: 8px;
    background-image: url('../img/ch-arr-bottom.svg');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 100% 100%; }

  .ui-field-btn {
    right: 9px; } }

@include rmin($lg) {
  .setting-open-button {
    top: 30%;
    width: 48px;
    height: 48px;
    border-radius: 48px 0 0 48px;
    padding-right: 10px; } }
