.schedule-item {
  display: block;
  width: 100%;
  max-width: 560px;
  margin: 0 auto 16px;
  border-radius: $smallRadius;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);

  &:last-child {
    margin-bottom: 0; } }

.schedule-info-line {
  display: flex;
  align-items: center;
  padding: 3px 16px;
  line-height: 22px;
  background-color: $colorExtra;
  border-radius: $smallRadius $smallRadius 0 0;
  color: $colorYellowLight;

  &__time {
    margin-right: 15px;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px; } }

.schedule-inner {
  padding: 16px 16px 6px; }

.shedule-title {
  h2, .h2-title {
    font-family: $baseFont, $systemFont;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    a {
      color: $fontColor;

      &:hover {
        color: $colorPrimary; } } } }

.user-list-wrapper {
  overflow-x: auto;
  min-width: 100%;
  margin-right: -16px;
  padding-bottom: 15px; }

.user-list {
  padding: 0;
  margin: 0;
  white-space: nowrap;

  li {
    display: inline-block;
    vertical-align: top;
    margin: 10px 16px 0 0;
    padding: 0; }

  &__img {
    position: relative;
    overflow: hidden;
    margin-right: 8px;
    width: 32px;
    height: 32px;
    border-radius: 20px;
    float: left;

    img {
      position: absolute;
      left: 50%;
      top: 50%;
      height: 100%;
      max-width: none;
      min-width: 100%;
      transform: translate(-50%, -50%); } }

  &__position {
    color: $fontColor50; } }

.user-list-info {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
  line-height: 16px; }


@include rmin($lg) {
  .schedule-item {
    max-width: 760px;
    margin-bottom: 24px; }

  .shedule-title {
    h2, .h2-title {
      min-height: 48px;
      font-size: 20px;
      line-height: 24px; } }

  .user-list {
    li {
      margin-top: 4px;
      margin-bottom: 6px; } }

  .schedule-btns {
    margin-top: 10px;
    margin-bottom: 6px;

    .btn {
      margin-right: 6px; } } }
