.live-chat-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  transform: translateX(100%);
  width: 100%;
  height: 100%;
  z-index: 250;
  transition: background-color $transitionSpeed;

  &.mobile-chat-opened {
    transform: translateX(0);
    background-color: rgba(#000, .56);

    .live-chat-inner {
      transform: translateX(0); } } }

.live-chat-inner {
  position: absolute;
  right: 0;
  transform: translateX(100%);
  width: 260px;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: linear-gradient(92.4deg, #A5F9E2 -119.19%, #FDFAF2 105.06%), #E29FE1;
  transition: transform $transitionSpeed; }

.live-chat-messages {
  height: 100%;
  padding: 20px;
  overflow-y: auto;
  flex-grow: 1; }

.chat-message {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 16px;

  &:last-child {
    margin-bottom: 0; }

  &__user-name {
    margin-bottom: 8px;
    font-weight: 700; }

  &__time {
    margin-bottom: 8px;
    color: rgba(0, 0, 0, .45); }

  &__text {
    color: rgba(0, 0, 0, .75);
    opacity: .75; } }

.chat-question-panel {
  padding: 15px 20px 20px;
  border: 1px solid rgba(31, 32, 65, .12);

  &__title {
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 18px;
    color: #6D7885;
    letter-spacing: -0.154px; } }

.chat-question-controls {
  position: relative;

  .ui-input {
    padding-right: 40px;

    &::placeholder {
      color: #000; } }

  button {
    position: absolute;
    right: 10px;
    top: 50%;
    margin-top: -12px;
    z-index: 2; } }

@include rmin($mobile-s) {
  .live-chat-inner {
    width: 300px; } }

@include rmin($lg) {
  .live-chat-wrapper {
    position: relative;
    min-width: 300px;
    max-width: 300px;
    height: auto;
    min-height: 100%;
    transform: none;
    z-index: 10; }

  .live-chat-inner {
    width: 100%;
    transform: none; } }

