.mobile-navi {
  display: none;
  // display: flex
  flex-direction: column;
  align-items: flex-end;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  background: rgba(0, 0, 0, .56);

  &__wrap {
    width: 260px;
    height: 100%;
    background-color: #fff;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column; } }

.header {
  &__hamburger {
    height: 56px;
    width: 56px;
    margin-left: 4px;
    padding: 0;
    margin-right: -16px;
    background: none;
    border: none;
    outline: none;
    font-size: 12px;
    color: #fff;

    svg {
      vertical-align: middle; } }

  &__menu {
    font-size: 18px;
    margin: 0;
    padding: 24px;

    li {
      display: block;
      margin-bottom: 16px;
      font-weight: 500;
      font-size: 18px;
      line-height: 24px;
      text-transform: uppercase;

      &.active {
        a {
          color: $colorPrimary; } } }

    a {
      color: $fontColor;
      transition: $transitionSpeed $ease;

      &:hover {
        color: $colorPrimary; } } } }

.mobile-logo {
  padding: 38px 20px 30px;
  text-align: center;
  background-color: $colorPrimary; }

@include rmin($mobile-s) {
  .mobile-navi {
    &__wrap {
      width: 300px; } } }

@include rmin($lg) {
  .header {
    &__hamburger {
      display: none; }

    &__menu {
      padding: 0;

      li {
        display: inline-block;
        vertical-align: top;
        margin: 10px 6px;
        font-size: 11px;

        &.active {
          position: relative;

          &::before {
            content: '';
            position: absolute;
            left: 50%;
            bottom: -10px;
            width: 48px;
            margin-left: -24px;
            border-radius: 4px;
            height: 4px;
            background-color: $colorPrimary; }

          a {
            color: $colorWhite;
            &:hover {
              cursor: default;
              color: $colorWhite; } } }

        &.hidden-desktop {
          display: none; }

        a {
          color: $colorWhite; } } } }

  .mobile-navi {
    display: block;
    height: auto;
    position: static;
    background: none;
    width: auto;
    margin: 0 auto;

    &__wrap {
      height: auto;
      width: auto;
      background: none; } }

  .mobile-logo {
    display: none; } }

@include rmin($xl) {
  .header {
    &__menu {
      li {
        font-size: 13px;
        margin: 0 14px; } } } }


@include rmin($hd) {
  .header {
    &__menu {
      li {
        font-size: 15px;
        letter-spacing: 0;
        margin: 10px 14px;

        &.active {
          &::before {
            bottom: -19px; } } } } } }
