.main-section {
  padding-top: 92px;
  padding-right: 20px;
  padding-bottom: 94px;
  color: $colorWhite;
  background-color: #d9d9d9;
  background-image: url('../img/banner.jpg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;

  .container {
    position: relative;
    z-index: 5; }

  .shape-ptrn {
    right: -40px;
    top: 125px; }

  .btn {
    margin-right: 7px; }

  p {
    max-width: 420px;
    margin-bottom: 12px;
    font-size: 12px;
    line-height: 16px;
    color: rgba($colorWhite, 0.75); } }

.main-arrow {
  display: none; }

.date-line {
  display: flex;
  margin-bottom: 18px;
  font-weight: 500;
  font-size: 12px;
  line-height: 24px;

  span {
    padding: 0 8px; }

  &__date {
    background-color: $colorPink;
    color: $fontColor; }

  &__time {
    background-color: $colorPrimary;
    color: $colorWhite; }

  &__status {
    background-color: $colorExtra;
    color: $colorWhite; } }

@include rmin($md) {
  .main-section {
    min-height: 100vh;
    max-height: 905px;
    padding: 140px 0;
    display: flex;
    align-items: center;

    .shape-ptrn {
      right: 2%;
      top: 126px; }

    h1 {
      margin-bottom: 32px;
      max-width: 900px; }

    p {
      max-width: 770px;
      margin-top: 32px;
      margin-bottom: 32px;
      font-size: 16px;
      line-height: 24px;
      color: $colorWhite; } }

  .date-line {
    font-size: 20px; }

  .main-arrow {
    display: block;
    position: absolute;
    z-index: 4;
    bottom: 65px;
    left: 50%;
    width: 30px;
    margin-left: -15px;
    color: $colorWhite;
    font-size: 38px; } }

@include rmin($xl) {
  .main-section {
    .shape-ptrn {
      right: 175px; } } }
