.start-section {
  padding-top: 30px;
  padding-bottom: 58px;
  overflow: hidden;

  .container {
    position: relative;
    z-index: 5; }

  .shape-ptrn {
    left: -6px;
    top: 49px; } }

.start-wrapper {
  text-align: center; }

.start-counter {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 11px;
  font-family: $titleFont, $systemFont;
  font-weight: 900;
  font-size: 20px;
  line-height: 40px;
  color: #FDFAF2;

  &__tt {
    margin-left: 4px;
    display: block;
    font-family: $baseFont, $systemFont;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    text-align: left; } }

@include rmin($md) {
  .start-section {
    padding: 70px 0 96px; }

  .start-counter {
    margin-bottom: 20px;
    font-size: 64px;
    line-height: 1;

    &__tt {
      margin-bottom: 4px; } } }

@include rmin($lg) {
  .start-section {
    .shape-ptrn {
      left: auto;
      right: 32px;
      top: 64px; } } }
