.news-item {
  display: block;
  margin: 0 16px 0 0;
  padding: 0;
  min-width: 224px;
  max-width: 224px;

  &:last-child {
    margin-right: 0; }

  &__img {
    display: block;
    height: 126px;
    overflow: hidden;
    margin-bottom: 9px;
    position: relative;

    img {
      height: 100%;
      max-width: none; } }

  &__tt {
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    a {
      color: $colorYellowLight;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } } }

  p {
    font-size: 12px;
    line-height: 16px; } }

@include rmin($md) {
  .news-item {
    margin-right: 30px; }

  .news-item {
    min-width: 360px;
    max-width: 360px;
    margin-right: 40px;

    &__img {
      height: 203px;
      margin-bottom: 18px; }

    &__tt {
      font-size: 20px;
      line-height: 24px; }

    p {
      font-size: 16px;
      line-height: 24px; } } }
