/* UI */

// WORKS KIND OF bootstrap - group have label and input
.ui-group {
  position: relative;
  margin-bottom: 20px;
  label {
    display: block;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.154px;
    margin-bottom: 8px; } }

// for input or textarea element
.ui-input {
  -webkit-appearance: none;
  display: block;
  width: 100%;
  background: white;
  font-family: $baseFont, $systemFont;
  color: $fontColor;
  font-size: 16px;
  line-height: 20px;
  border: 1px solid $borderColor;
  border-radius: $smallRadius;
  padding: 11px 12px;
  letter-spacing: -0.32px;
  background-color: $bgInput;
  transition: $transitionSpeed $ease;

  &:focus {
    border-color: $colorGreen; }

  &::placeholder {
    color: $fontColorLight; }

  &.has-error {
    border-color: $colorRed; } }

.ui-input textarea {
  resize: none; }

/* Checkbox */
.ui-checkbox,
.ui-radio {
  padding-top: 12px;
  padding-bottom: 12px;

  input {
    display: none;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;

    &:checked {
      + label {
        &::before {
          border-color: $colorPrimary; }

        &::after {
          transform: translateY(-50%) scale(1); } } } }

  label {
    display: block;
    width: 100%;
    position: relative;
    cursor: pointer;
    margin-bottom: 0;

    &::before {
      position: absolute;
      left: 15px;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      content: ' ';
      size: 14px;
      background-color: white;
      border: 2px solid $colorLightGray;
      border-radius: 2px;
      cursor: pointer;
      vertical-align: middle;
      cursor: pointer;
      transition: all $transitionSpeed ease-in-out;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: 100% 100%; }

    span {
      display: inline-block;
      padding-left: 48px;
      vertical-align: top;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.32px;
      cursor: pointer; } } }

.ui-checkbox {
  input {
    &:checked {
      + label {
        &::before {
          size: 18px;
          border: none;
          background-image: url('../img/check.svg'); } } } }

  label {
    &::before {
      transition: none; } } }

.ui-radio {
  label {
    &::before {
      border-radius: 50%;
      size: 16px; }

    &::after {
      position: absolute;
      left: 20px;
      top: 50%;
      transform: translateY(-50%) scale(0);
      display: inline-block;
      content: ' ';
      size: 10px;
      background: $colorPrimary;
      border-radius: 50%;
      transition: transform .2s ease; } } }

// form ui
.ui-label {
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.154px;
  color: #575870; }

.ui-field-group {
  display: flex;
  align-items: flex-start; }

.ui-field {
  position: relative;
  flex-grow: 1;

  &__info {
    font-size: 12px;
    line-height: 16px;
    letter-spacing: -0.32px;
    color: rgba(31, 32, 65, 0.38); }

  input {
    height: 44px;
    padding-right: 50px; } }

.ui-field-btn {
  position: absolute;
  z-index: 3;
  right: 0;
  top: 0;
  height: 44px;
  width: 44px;
  text-align: center;
  background: none;
  outline: none;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #A3A3B1;
  transition: $transitionSpeed $ease;

  &:hover {
    color: $colorDarkGray; }

  .ico-mono-upload {
    font-size: 18px; }

  .ico-mono-paint {
    font-size: 24px; }

  .ico-mono-ch-arr-bottom {
    font-size: 8px; }

  .ico-mono-bin {
    font-size: 44px; } }
