.label-info {
  display: inline-block;
  vertical-align: top;
  padding: 0 4px;
  border-radius: 4px;
  font-weight: 500;
  font-size: 10px;
  line-height: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 100%;
  white-space: nowrap;
  color: $colorYellowLight;
  background: #ccc;

  &--mcolor {
    background-color: $colorPink; }

  &--primary {
    background-color: $colorPrimary; }

  &--extra {
    background-color: $colorExtra; }

  &--text-dark {
    color: #000; } }

.label-info + .label-info {
  margin-left: 4px; }
