* {
  box-sizing: border-box;
  outline: none; }

a {
  color: $colorPrimary;
  text-decoration: none;
  transition: $transitionSpeed $ease;

  &:hover, &:focus {
    text-decoration: none;
    color: $colorPrimaryHover; } }

img {
  max-width: 100%; }

ul {
  list-style: none;

  li {
    display: inline-block; } }

body, html {
  background: #fff;
  color: $fontColor;
  font-family: $baseFont, $systemFont;
  // font-family: "Helvetica", -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Helvetica', 'Ubuntu', 'Cantarell', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol'
  font-size: 16px;
  line-height: 24px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

::selection {
  color: white;
  background: $colorPrimary; }

.is-ios {
  cursor: pointer;
  * {
    cursor: pointer; } }

.body-lock {
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  &.is-mobile {
    overflow: visible; } }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding-top: 56px;

  &__content {
    // used for sticky footer (flexbox way)
    flex: 1 0 auto;
    display: flex;
    flex-direction: column; }

  &.homepage {
    padding-top: 0; } }

.container {
  max-width: 1200px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px; }

.container-fluid {
  padding-left: 16px;
  padding-right: 16px; }

.inner-section {
  position: relative;
  z-index: 5;
  margin-top: -32px;
  padding-top: 25px;
  padding-bottom: 72px;
  border-radius: $largeRadius $largeRadius 0 0;
  background-color: #fff;

  &--primary-grad-color {
    background: linear-gradient(114.56deg, $colorPrimary 19.77%, #FDFAF2 200.92%), #E29FE1;
    color: $colorYellowLight;

    .inner-section__head {
      p {
        color: rgba(#FDFAF2, .75); } } }

  &--primary-color {
    background-color: $colorPrimary;
    color: $colorYellowLight;

    .inner-section__head {
      p {
        color: rgba(#FDFAF2, .75); } } }

  &--extra-color {
    background-color: $colorExtra;
    color: $colorYellowLight;

    .inner-section__head {
      p {
        color: rgba(#FDFAF2, .75); } } }

  &__head {
    margin-bottom: 30px;

    p {
      max-width: 560px;
      margin: 24px auto 0;
      text-align: center;
      color: $colorDarkGray; } } }

.scroll-wrapper {
  overflow-x: auto;
  margin-right: -16px;
  margin-left: -16px;
  padding-bottom: 15px;

  > div, > ul {
    padding-left: 16px; } }

.st-md-form {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto; }

@include rmin($md) {
  .inner-section {
    padding-top: 68px;
    padding-bottom: 100px;

    &__head {
      margin-bottom: 48px; } } }

@include r($lg) {
  .hidden-mobile {
    display: none; } }

@include rmin($lg) {
  .container,
  .container-fluid {
    padding-left: 20px;
    padding-right: 20px; }

  .scroll-wrapper {
    margin-right: -20px;
    margin-left: -20px;

    > div, > ul {
      padding-left: 20px; } } }

.shape-ptrn {
  width: 79px;
  height: 79px;
  position: absolute;
  z-index: 1;
  background-image: url('../img/dott-ptrn.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 100% 100%; }

@include rmin($md) {
  .shape-ptrn {
    width: 210px;
    height: 210px; } }

@include rmin($xl) {
  .container-fluid {
    padding-left: 28px;
    padding-right: 28px; } }

@include rmin($hd) {
  .page {
    padding-top: 96px; } }
