.partner-item {
  display: block;
  height: 164px;
  position: relative;
  background-color: $colorWhite;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  margin: 0 0 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px; }

.partner-label-wrapp {
  position: absolute;
  top: 8px;
  left: -8px;
  max-width: 100%;
  width: 100%; }

.partners-list--mini {
  .partner-item {
    height: 80px;
    width: 48.5%; } }

@include rmin($md) {
  .partner-item {
    width: 32%;
    height: 120px; }

  .partners-list--mini {
    .partner-item {
      width: 15%; } } }

@include rmin($lg) {
  .partner-item {
    height: 180px;
    margin-bottom: 40px; }

  .partners-list--mini {
    .partner-item {
      margin-bottom: 20px; } } }

@include rmin($hd) {
  .partner-item {
    width: 360px; }

  .partners-list--mini {
    .partner-item {
      width: 160px; } } }

