.inner-schedule {
  padding-top: 0;
  padding-bottom: 75px;

  .btn-more-wrap {
    margin-top: 25px; } }

.inner-schedul-head {
  text-align: center; }

.schedule-scroll-wrapper {
  display: flex;
  justify-content: space-between;
  overflow-x: auto;
  padding-bottom: 15px; }

.schedule-list {
  padding: 0;
  margin: 0; }

.schedule-columns--two-cols {
  .schedule-list {
    margin-right: 16px; } }

.sh-col-places {
  text-align: center;
  padding: 0;
  margin: 0 0 24px;
  line-height: 16px;

  li {
    vertical-align: top;
    margin: 0 22px;
    font-weight: 500;
    font-size: 16px;
    color: $fontColorLight;

    &.active {
      color: $fontColor; } } }

@include rmin($md) {
  .schedule-columns {
    &--two-cols {
      display: flex;
      flex-wrap: wrap;
      position: relative;
      justify-content: space-between;
      margin-top: 25px;

      &::before {
        content: '';
        position: absolute;
        left: 50%;
        top: 0;
        margin-left: -1px;
        width: 2px;
        height: 100%;
        z-index: 2;
        background: rgba(31, 32, 65, 0.05); }

      &::after {
        content: '';
        position: absolute;
        left: 50%;
        top: 0;
        margin-left: -8px;
        width: 8px;
        height: 8px;
        z-index: 2;
        background: #fff;
        border: 4px solid rgba(31, 32, 65, 0.05);
        border-radius: 16px; }

      .schedule-list {
        width: 48%;
        margin-right: 0; } } }

  .sh-col-places {
    width: 100%;
    margin-bottom: 16px;

    li {
      margin: 0 20px;
      color: $fontColor; } } }

@include rmin($lg) {
  .schedule-columns--two-cols {
    margin-top: 28px; }

  .inner-schedule {
    padding: 30px 0 140px;

    .btn-more-wrap {
      margin-top: 66px; } } }
