.search-wrapp {
  position: fixed;
  bottom: 16px;
  right: 16px;
  height: 56px;
  z-index: 30; }

.open-search {
  .search-form {
    display: block; } }

.search-form {
  display: none;
  width: 290px;
  height: 56px;
  position: relative;
  background-color: $colorExtra;
  border-radius: 28px;

  input {
    width: 100%;
    height: 56px;
    padding: 0 76px 0 27px;
    color: $colorWhite;
    background: none;
    border: none;

    &::placeholder {
      color: rgba($colorWhite, .75); } } }

.search-button {
  position: absolute;
  top: 0;
  right: 1px;
  width: 56px;
  height: 56px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 18px;
  color: $colorWhite;
  background-color: $colorExtra;
  outline: none;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  transition: $transitionSpeed $ease;

  &:hover {
    background-color: darken($colorExtra, 5%); } }

@include rmin($mobile-s) {
  .search-form {
    width: 328px; } }

@include rmin($hd) {
  .search-wrapp {
    right: 72px;
    bottom: 72px; } }
