.about-video {
  width: 182px;
  height: 182px;
  margin: 0 auto 40px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    right: -10px;
    bottom: -10px;
    width: 178px;
    height: 178px;
    z-index: 2;
    background: $colorPrimary;
    border-radius: 152.5px 0px 0px 0px; }

  &.active {
    video {
      display: block; }

    .about-video__img {
      opacity: 0; } }

  video {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
    height: 100%;
    z-index: 7; }

  &__play {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: none;
    border: none;
    outline: none;
    padding: 0;
    transition: $transitionSpeed $ease; }

  &__img {
    position: relative;
    z-index: 5;
    width: 100%;
    height: 100%;
    overflow: hidden;
    opacity: 1;
    transition: $transitionSpeed $ease;

    img {
      position: absolute;
      min-width: 100%;
      max-width: none;
      height: 100%;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%); } } }

.about-info {
  max-width: 490px;
  margin: 0 auto; }

.about-info-list {
  display: flex;
  justify-content: space-between;
  margin: 0 0 20px;
  padding: 0;

  li {
    font-family: $titleFont, $systemFont;
    font-weight: 900; }

  strong {
    display: block;
    font-size: 24px;
    line-height: 1; }

  span {
    display: block;
    font-size: 16px;
    line-height: 24px; } }

@include rmin($md) {
  .about-inner {
    margin-top: 30px;
    margin-bottom: 65px; } }

@include rmin($lg) {
  .about-wrapp {
    display: flex;
    justify-content: space-between;
    max-width: 964px;
    margin: 65px auto; }

  .about-info {
    max-width: 564px;
    margin-right: 0;

    p {
      margin-top: 30px; } }

  .about-info-list {
    max-width: 490px;
    margin-bottom: 30px; }

  .about-video {
    margin: 0 30px 0 0;
    width: 312px;
    height: 312px;

    &::before {
      right: -18px;
      bottom: -18px;
      width: 305px;
      height: 305px; } } }
