.static-inner {
  max-width: 760px;
  margin-left: auto;
  margin-right: auto; }

.static-labels-title {
  margin-bottom: 24px;

  h1 {
    margin-bottom: 8px;
    color: $fontColor; }

  .label-info + .label-info {
    margin-left: 8px; } }

.static-inner {
  font-size: 14px;
  line-height: 20px;
  color: $colorDarkGray;

  p {
    margin-bottom: 20px;

    b {
      font-weight: 700; } }

  p + p {
    margin-top: 20px; }

  .text-md-size {
    font-size: 16px;
    line-height: 24px; }

  ul {
    padding: 0;
    margin: 0;

    li {
      margin: 0;
      padding: 0;
      position: relative;
      display: block;

      &::before {
        content: '•';
        margin-right: 15px; } } } }

.static-picture-bl {
  margin: 20px -16px;

  img, video {
    width: 100%; } }

@include rmin($mobile-s) {
  .static-labels-title {
    h1 {
      font-size: 32px;
      line-height: 40px; } } }

@include rmin($md) {
  .static-labels-title {
    h1 {
      font-size: 48px;
      line-height: 1em;
      margin-bottom: 20px; } }

  .static-inner {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 85px;

    p {
      margin-bottom: 24px; }

    p + p {
      margin-top: 24px; }

    .text-md-size {
      font-size: 20px;
      line-height: 32px; } }

  .static-picture-bl {
    margin: 40px -16px; } }

@include rmin($lg) {
  .static-inner {
    padding-top: 5px; }

  .static-picture-bl {
    margin-left: -100px;
    margin-right: -100px; } }
