.live-main-video {
  margin: 0 -16px 16px;

  img {
    vertical-align: top; } }

.live-main-info {
  margin-bottom: 34px;

  h1, .h1-title {
    margin-bottom: 8px;
    font-size: 16px;
    line-height: 20px; }

  .label-info {
    margin-bottom: 8px; }

  p {
    margin: 0 0 20px;
    font-size: 12px;
    line-height: 16px;
    color: $colorDarkGray; } }

.videos-section {
  padding: 20px 0 30px; }

.videos-header {
  .h2-title {
    margin-bottom: 24px;
    font-size: 32px;
    line-height: 56px;
    text-align: center; }

  .tabs-wrapper {
    margin-bottom: 5px; } }

@include rmin($md) {
  .live-main-info {
    margin-bottom: 48px;

    h1, .h1-title {
      font-size: 32px;
      line-height: 36px; }

    p {
      font-size: 16px;
      line-height: 24px; }

    .btn {
      display: none; } }

  .videos-header {
    .h2-title {
      font-size: 48px;
      margin-bottom: 16px; } } }

@include rmin($lg) {
  .live-main-bl {
    display: flex;
    margin-bottom: 14px; }

  .live-main-video {
    margin: 0;
    flex-grow: 1; }

  .live-main-info {
    padding-right: 300px; } }
