// titles
h1, h2, h3, h4, .h1-title, .h2-title, .h3-title, .h4-title {
  font-family: $titleFont, $systemFont;
  font-weight: 900; }

.section-title {
  margin-bottom: 12px;
  text-align: center;
  font-family: $titleFont, $systemFont;
  font-weight: 900;
  font-size: 32px;
  line-height: 56px; }

.h1-title, h1 {
  margin: 0 0 15px;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: em(-1.22, 72); }

.h2-title, h2 {
  margin: 0 0 15px;
  font-size: 20px;
  line-height: 28px; }

.h3-title, h3 {
  margin: 0 0 15px;
  font-size: 18px;
  line-height: 24px; }

// paragraphs
.text-sm {
  font-size: 12px;
  line-height: 16px; }

p {
  margin-bottom: 0;
  margin-top: 0; }

p + p {
  margin-top: 25px; }

.text-center {
  text-align: center; }

.text-light {
  color: rgba($fontColor, .64); }

.text-dark {
  color: #000; }

.text-primary {
  color: $colorPrimary; }

.text-extra {
  color: $colorExtra; }

@include rmin($md) {
  .h1-title, h1 {
    font-size: 56px;
    line-height: 1; }

  .h2-title, h2 {
    font-size: 48px;
    line-height: 1; }

  .h3-title, h3 {
    font-size: 36px;
    line-height: 1.1; }

  .section-title {
    margin-bottom: 25px;
    font-size: 48px;
    line-height: 1; } }


// little inline helpers
.f-400 {
  font-weight: normal; }

.f-500 {
  font-weight: 500; }

.f-600 {
  font-weight: 600; }

.f-700 {
  font-weight: 700; }

.f-900 {
  font-weight: 900; }

// color modifiers
.c-red {
  color: $colorRed; }
