.play-video-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 44px;
  height: 44px;
  padding: 0;
  background: rgba(0, 0, 0, .37);
  border: 2px solid $colorWhite;
  border-radius: 22px;
  color: #fff;
  font-size: 15px;
  outline: none;
  transition: $transitionSpeed $ease;

  &:hover {
    transform: translate(-50%, -50%) scale(1.1); }

  svg {
    position: relative;
    left: 1px;
    top: -1px; } }
