// LAYOUT
$mobile-s: 375;
$sm: 576;
$md: 768;
$lg: 992;
$xl: 1200;
$hd: 1440;

// FONTS
$baseFont: "Roboto";
$titleFont: "Exo 2";
$systemFont: -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Helvetica', 'Ubuntu', 'Cantarell', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';

// COLORS
$colorPrimary: #4EAC93;
$colorExtra: #4A50C0;
$colorPink: #E29FE1;

$colorPrimaryText: darken($colorPrimary, 10%);
$colorPrimaryHover: darken($colorPrimary, 5%);
$fontColor: #1F2041;
$fontColor50: rgba($fontColor, .5);
$fontColorLight: rgba($fontColor, .38);
$colorGreen: #27AE60;
$colorRed: #FE3737;
$colorRedHover: lighten($colorRed, 15%);
$colorDarkBrue: #1F2041;
$colorDarkGray: rgba(#1F2041, .75);
$colorLightGray: rgba(#1F2041, .38);
$colorWhite: #fff;
$borderColor: rgba(#000, .12);
$colorBg: #E5E5E5;
$colorYellowLight: #FDFAF2;
$bgInput: rgba(31, 32, 65, .05);
$bgDark: #1F2041;

// RADIUS
$smallRadius: 10px;
$largeRadius: 32px;

// OPTIONS
$transitionSpeed: .25s;
$ease: ease-in-out;

// SOCIALS
$twitter: #00aced;
$facebook: #3b5998;
$googleplus: #dd4b39;
$pinterest: #cb2027;
$linkedin: #007bb6;
$youtube: #bb0000;
$vimeo: #1ab7ea;
$tumblr: #32506d;
$instagram: #bc2a8d;
$flickr: #ff0084;
$dribbble: #ea4c89;
$quora: #a82400;
$foursquare: #0072b1;
$forrst: #5B9A68;
$vk: #45668e;
$wordpress: #21759b;
$stumbleupon: #EB4823;
$yahoo: #7B0099;
$blogger: #fb8f3d;
$soundcloud: #ff3a00;

// BS GRID
$col1: 8.333333%;
$col2: 16.666667%;
$col3: 25%;
$col4: 33.333333%;
$col5: 41.666667%;
$col6: 50%;
$col7: 58.333333%;
$col8: 66.666667%;
$col9: 75%;
$col10: 83.333333%;
$col11: 91.666667%;
$col12: 100%;
