// FOOTER
.footer {
  padding: 80px 0 50px;
  background: $bgDark;
  color: $colorYellowLight;
  text-align: center;

  p {
    padding:  0 10px;
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 54px; } }

.footer-logo {
  width: 204px;
  margin: 0 auto 28px;

  a {
    display: block;

    img {
      vertical-align: top; } } }

.footer-nav {
  margin-top: 22px;

  ul {
    padding: 0;
    margin: 0; }

  li {
    margin: 0;

    a {
      display: inline-block;
      padding: 0 8px;
      font-size: 15px;
      line-height: 36px;
      color: $colorWhite;

      &:hover {
        color: $colorPrimaryHover; } } } }


@include rmin($md) {
  .footer-nav {
    li {
      margin: 0 7px; } } }
