.tabs-wrapper {
  overflow: auto;
  padding: 0 15px 15px;
  margin: 5px -15px 10px; }

.tabs-btns {
  display: flex;
  width: 100%;
  padding: 0;
  margin: 0;
  text-align: center;

  li {
    padding: 6px;
    // margin: 5px
    // border-radius: 28px
    background: rgba(31, 32, 65, 0.04);

    &:first-child {
      margin-left: auto;
      border-radius: 28px 0 0 28px; }

    &:last-child {
      margin-right: auto;
      border-radius: 0 28px 28px 0; }

    span {
      display: block;
      padding: 0 20px;
      min-width: 90px;
      text-align: center;
      font-weight: 500;
      font-size: 14px;
      line-height: 32px;
      white-space: nowrap;
      cursor: pointer;
      border-radius: 44px;
      transfion: $transitionSpeed $ease;

      &:hover {
        opacity: .8; } }

    &.active {
      span {
        color: $colorYellowLight;
        background-color: $colorPrimary;
        box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
        cursor: default;

        &:hover {
          opacity: 1; } } } } }

@include rmin($mobile-s) {
  .tabs-btns {
    li {
      border-radius: 0;
      margin: 0;

      &:first-child {
        border-radius: 28px 0 0 28px; }

      &:last-child {
        border-radius: 0 28px 28px 0; } } } }

@include rmin($lg) {
  .tabs-wrapper {
    padding-left: 0;
    margin: 15px 0 30px; } }
