.contacts-bl {
  text-align: center;
  margin-bottom: 28px; }

.contact-item {
  font-size: 24px;
  line-height: 34px;

  &__phone {
    color: $colorDarkGray; } }


@include rmin($lg) {
  .section-contacts {
    padding: 44px 0 100px;

    .inner-section__head {
      margin-bottom: 55px; } }

  .contact-item {
    font-size: 56px;
    line-height: 66px; } }
