.inner-banner {
  text-align: center;
  background-color: #2A2046;
  background-image: url('../img/inner-banner-mob.jpg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  color: $colorWhite;

  .container {
    padding-top: 50px;
    padding-bottom: 50px;
    position: relative; }

  h1, .h1-title {
    margin-bottom: 0;
    font-size: 32px;
    line-height: 56px; } }

.inner-banner-back-btn {
  position: absolute;
  left: 8px;
  top: 8px;
  z-index: 2;

  .btn {
    color: $colorWhite;

    &:hover {
      color: $colorPrimary; } } }

@include rmin($md) {
  .inner-banner {
    background-image: url('../img/inner-banner.jpg');

    h1, .h1-title {
      font-size: 48px; } }

  .inner-banner-back-btn {
    top: 12px;
    left: 20px; } }
