/* HEADER */
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 200;
  background: linear-gradient(180deg, rgba(12, 13, 24, 0.5) -93.23%, rgba(31, 32, 65, 0) 100%);

  &.open-menu {
    z-index: 250;

    .mobile-navi {
      display: flex; } }

  &.sticky-menu {
    background-color: #1F2041; }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between; }

  &__logo {
    max-width: 138px;
    flex: 0 0 auto;
    font-size: 35px;
    margin-right: 30px;

    img {
      vertical-align: middle; } } }

.header-min-nav {
  padding: 0;
  margin: 0;
  margin-left: auto;

  li {
    display: none;
    font-weight: 500;
    font-size: 12px;
    line-height: 56px;

    &.header-min-nav__watch {
      display: block;

      a {
        display: block;
        padding: 0 12px;
        background: $colorPrimary;

        &:hover {
          background: $colorPrimaryHover;
          color: $colorWhite; } } }

    a {
      color: #fff;
      transition: $transitionSpeed $ease; } } }

@include rmin($lg) {
  .header-min-nav {
    margin-left: 30px;
    white-space: nowrap;

    li {
      display: inline-block;
      vertical-align: top;
      margin-left: 15px;

      &.header-min-nav__watch {
        display: inline-block; } } } }

@include rmin($hd) {
  .header {
    &__logo {
      max-width: 204px;
      position: relative;
      top: 4px; } }

  .header-min-nav {
    margin-left: 30px;
    white-space: nowrap;

    li {
      display: inline-block;
      vertical-align: top;
      font-size: 15px;
      line-height: 96px;

      a {
        display: block;
        padding-left: 16px;
        padding-right: 16px;

        &:hover {
          color: $colorPrimary; } }

      &.header-min-nav__watch {
        a {
          padding-left: 16px;
          padding-right: 16px; } } } } }
