.bread-crumbs {
  padding: 0;
  margin: 16px 0;

  li {
    vertical-align: top;
    padding: 0;
    margin: 0;

    &:last-child {
      margin-right: 0; } }

  .btn {
    color: $colorExtra;

    &:hover {
      color: lighten($colorExtra, 5%); } } }

@include rmin($md) {
  .bread-crumbs {
    margin: 12px 0 16px;

    li {
      margin-right: 14px;

      &:last-child {
        margin-right: 0; } } } }
