.speaker-item {
  display: flex;
  margin-bottom: 20px;
  padding: 4px;
  background-image: url('../img/dott-square.svg');
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 60px;

  &__photo {
    overflow: hidden;
    position: relative;
    min-width: 64px;
    max-width: 64px;
    height: 80px;
    margin-right: 16px;

    img {
      position: absolute;
      left: 50%;
      top: 50%;
      max-width: none;
      height: 100%;
      min-width: 100%;
      transform: translate(-50%, -50%); }

    .play-video-btn {
      left: auto;
      top: auto;
      transform: none;
      right: 4px;
      bottom: 4px;
      width: 16px;
      height: 16px;
      border-width: 1px;
      font-size: 5px;

      svg {
        margin: 1px 0 0 -1px; } } }

  &__name {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px; } }

.speaker-item-info {
  p {
    margin-top: 4px;
    margin-bottom: 0;
    font-size: 12px;
    line-height: 16px;
    color: $colorDarkGray; }

  .btn {
    display: none; } }

.all-speakers-btn {
  margin-left: -16px;
  margin-right: -16px;
  margin-top: 28px;
  padding: 24px 36px;
  background: linear-gradient(92.4deg, #A5F9E2 -119.19%, #FDFAF2 105.06%), #E29FE1; }

.all-speakers-btn-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 288px;
  margin-left: auto;
  margin-right: auto; }

.all-peakers-btn-text {
  font-family: $titleFont, $systemFont;
  font-weight: 900;
  font-size: 24px;
  line-height: 24px;

  span {
    display: block;
    font-size: 16px; } }

@include rmin($md) {
  .speaker-item {
    width: 49%; }

  .all-speakers-btn {
    max-width: 376px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 48px; } }

@include rmin($lg) {
  .speakers-section {
    padding-top: 86px;
    padding-bottom: 128px; }

  .speaker-item {
    margin-bottom: 60px;
    padding: 11px 4px 4px 13px;
    background-size: 133px;

    &__photo {
      min-width: 200px;
      max-width: 200px;
      height: 264px;
      margin-right: 20px;

      .play-video-btn {
        right: 8px;
        bottom: 8px;
        width: 44px;
        height: 44px;
        border-width: 2px;
        font-size: 16px;

        &:hover {
          transform: scale(1.1); } } }

    &__name {
      margin: 4px 0 8px;
      font-size: 20px;
      line-height: 26px; } }

  .speaker-item-info {
    p {
      margin-bottom: 24px;
      font-size: 16px;
      line-height: 24px; }

    .btn {
      display: inline-block; } } }

@include rmin($xl) {
  .speaker-item {
    width: 560px;

    &__photo {
      min-width: 260px;
      max-width: 260px;
      height: 324px; } } }
