.st-slider {
  position: relative;
  display: flex; }

.st-slider-item {
  position: relative;
  min-width: 288px;
  max-width: 288px;
  z-index: 3;
  border-radius: $smallRadius;
  padding: 28px;
  overflow: hidden;
  margin-right: 16px;

  &:last-child {
    margin-right: 0; }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    background: linear-gradient(-270deg, #293250 0%, rgba(41, 50, 80, 0) 100%); }

  img {
    position: absolute;
    left: 50%;
    top: 50%;
    max-width: none;
    height: 100%;
    min-width: 100%;
    z-index: 2;
    transform: translate(-50%, -50%); }

  &__inner {
    position: relative;
    z-index: 5;

    p {
      font-size: 14px;
      line-height: 24px;
      color: rgba($colorWhite, .85);
      margin-bottom: 16px; } }

  .btn {
    position: relative;
    z-index: 5; } }

.st-lider-item-tt {
  font-family: $titleFont, $systemFont;
  font-weight: 700;
  font-size: 20px;
  line-height: 32px;
  padding-bottom: 10px;
  margin-bottom: 24px;
  border-bottom: 2px solid $colorWhite;

  &.primary-border-b {
    border-color: $colorPrimary; }

  &.extra-border-b {
    border-color: $colorExtra; }

  &.more-border-b {
    border-color: $colorPink; } }

.slider-nav {
  position: absolute;
  left: 0;
  height: 1px;
  width: 100%;
  top: 50%;
  z-index: 10;
  display: flex;
  justify-content: space-between;

  &.hidden-mobile {
    display: none; }

  &__prev,
  &__next {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -22px;
    outline: none;
    width: 44px;
    height: 44px;
    font-size: 16px;
    color: $fontColor;
    border-radius: 50%;
    background-color: #FDFAF2;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
    transition: $transitionSpeed $ease;

    &:hover {
      background-color: $colorPrimary;
      color: $colorWhite; } } }

@include rmin($lg) {
  .st-slider-item {
    min-width: 360px;
    max-width: 360px;
    min-height: 284px;
    margin-right: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between; }

  .st-lider-item-tt {
    font-size: 24px; } }

@include rmin($hd) {
  .slider-nav {
    &.hidden-mobile {
      display: flex; } } }
